import { useState } from 'react';
import { LabelInput } from './LabelInput';

export function InitPage({ onOkBtnClick }) {
    const [gitUrl, setGitUrl] = useState('https://justcors.com/tl_484c81f/https://github.com/morrow1nd/ptdata.git');
    const [gitAuth, setGitAuth] = useState('');
    const [secretCode, setSecretCode] = useState('');

    return (
        <>
            <p><LabelInput value={gitUrl} onChange={setGitUrl} text="git url: " /></p>
            <p><LabelInput value={gitAuth} onChange={setGitAuth} text="git auth: " /></p>
            <p><LabelInput value={secretCode} onChange={setSecretCode} text="secret code: " /></p>
            <p><button onClick={() => {
                onOkBtnClick(gitUrl, gitAuth, secretCode);
            }}>OK</button></p>
        </>
    );
}
