import { useState } from 'react';
import { useImmer } from 'use-immer';
import { InitPage } from "./InitPage";
import { InitGitPage } from "./InitGitPage";

export default function App() {
    const [state, updateState] = useImmer({
        router: 'init'
    });

    switch (state.router) {
        case 'init':
            return <InitPage onOkBtnClick={(gitUrl, gitAuth, secretCode) => {
                updateState(draft => {
                    draft.gitUrl = gitUrl;
                    draft.gitAuth = gitAuth;
                    draft.secretCode = secretCode;
                    draft.router = 'init-git';
                });
            }} />;
        case 'init-git':
            return <InitGitPage state={state} updateState={updateState} />;
        default:
            throw 'unknown router value: ' + router;
    }
}
