import { useEffect, useState } from 'react';
import http from 'https://unpkg.com/isomorphic-git/http/web/index.js';

import LightningFS from '@isomorphic-git/lightning-fs';
// import http from 'isomorphic-git/http/web/index';
// import git from 'isomorphic-git';
// import git from "https://unpkg.com/isomorphic-git";

export function InitGitPage({ state, updateState }) {
    const [text, setText] = useState('git clone ...');

    useEffect(() => {
        const fs = new LightningFS('fs')
        const dir = '/test-clone3'

        setText('git clone: ' + state.gitUrl);

        git.clone({
            fs, http, dir,
            url: state.gitUrl,
            //corsProxy: 'https://cors.isomorphic-git.org'
        })
            .then((text) => {
                console.log("---- finish " + text);
                // setText(text);
                fs.readdir(dir, null, (err, files) => {
                    console.log(files);
                    setText(JSON.stringify(files));
                });
            })
            // .catch((error)=>{
            //     setText(JSON.stringify(error));
            // });
    }, [])

    return (
        <>
            <p>{text}</p>
        </>
    );
}
